import React, { Component } from 'react'
import request from '@/utils/request'
import Cookie from 'js-cookie'
import '@/static/css/login.scss'

class Login extends Component {
    state = {
        checkcodebtnTip:'获取验证码',
        phone:'',
        checkcode:'',
        password:''
    }
    render() {
        let {
            checkcodebtnTip,
            phone,
            checkcode,
            password
        } = this.state;
        return (
            <div className="wrap login-wrap">
                <div className="container">
                    <div className="login-left">
                        <p className="font">Welcome</p>
                        <p className="money">赚赚金融 开创信贷“1＋N”模式的综合互联网金融服务共享平台</p>
                    </div>
                    <div className="login-right">
                        <div className="login">
                            <dl>
                                <dt>
                                    <i className="iconfont iconlogo"></i>
                                </dt>
                                <dd>赚赚金融渠道管理系统</dd>
                            </dl>
                            <p>
                                <input type="text" 
                                    placeholder="手机号"
                                    value={phone}
                                    onChange={e=>this.setState({phone:e.target.value})}
                                />
                            </p>
                            <p>
                                <input type="password" 
                                    placeholder="登录密码"
                                    value={password}
                                    onChange={e=>this.setState({password:e.target.value})}
                                />
                            </p>
                            <p>
                                <input type="text" 
                                    placeholder="验证码"
                                    value={checkcode}
                                    onChange={e=>this.setState({checkcode:e.target.value})}
                                />
                                <span 
                                    className="checkcodebtn"
                                    onClick={this.changeCheckCode}>{checkcodebtnTip}</span>
                            </p>
                            <button onClick={this.login}>登录</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
        this.getCheckCode();
    }
    getCheckCode = ()=>{
        request.get('/api/checkCode').then(data=>{
            this.setState({checkcodebtnTip:data.Verification});
        })
    }
    changeCheckCode = ()=>{
        this.getCheckCode();
    }
    login = ()=>{
        let {
            phone,
            checkcode,
            password
        } = this.state;
        request.post('/api/login',{
            phone,
            checkcode,
            password
        }).then(res=>{
            if(res.code){
                alert(res.message);
            }else{
                Cookie.set('token',res.sessionId,{
                    expires:5
                });
                this.props.history.push('/home');
            }
        })
    }
}


export default Login;